body {
  text-align: center;
}
.content {
  margin-top: 80px;
}

input {
  font-size: 17px;
  width: 310px;
  height: 60px;
  padding: 0 10px;
  border: 2px solid #1489b9;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0 4px 15px 0 rgba(81, 82, 80, 0.75);
}

.btn {
  color: #fff;
  width: 195px;
  font-size: 17px;
  font-weight: 700;
  cursor: pointer;
  height: 60px;
  text-align: center;
  border: none;
  border-radius: 5px;
  background-size: 300% 100%;
  background-image: linear-gradient(to right, #1489b9, #149e19);
  box-shadow: 0 4px 15px 0 rgba(81, 82, 80, 0.75);
}

.timer {
  display: flex;
  text-align: center;
  justify-content: center;
  margin-top: 25px;
}

.field {
  width: 100px;
  padding: 10px;
  border-radius: 5px;
  background: #32b1e4;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 15px 0 rgba(81, 82, 80, 0.75);
  margin-right: 5px;
}

.value {
  padding: 10px;
  border-radius: 25px;
  background: #00816a;
  color: #fff;
  box-shadow: 0 4px 15px 0 rgba(81, 82, 80, 0.75);
}

.label {
  color: #fff;
}
